body
{
  margin:0;



background-color:black;
background-image:
radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 40px),
radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 30px),
radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 40px),
radial-gradient(rgba(255,255,255,.4), rgba(255,255,255,.1) 2px, transparent 30px);
background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px;
background-position: 0 0, 40px 60px, 130px 270px, 70px 100px;


   width:"100vw";
   height:"100vw";
}



.wrap
{
 display: block;
 width:100%;
  height: 100%;
  position: "absolute";
  top: "50%";
  left: "50%";

 transform:"translate(-50%,-50%)";
  
}

.con
{
  display: flex;
flex-wrap: wrap;
flex-direction: column;
align-items: center;
margin: 3% auto;
  width: 35%;
height: 20%;





}
.status
{  
font-family: "Times New Roman", Times, serif;
  font-size: 2em;
  color:white;
  margin-left: 2%; 

}

.b1
{font-family: "Times New Roman", Times, serif;
  background-color: #233;
  color: white;
  width:100px;
  height:35px;
 margin-top: 10%;
  border-radius:25px;
  font-size: 1em;
  letter-spacing: 1px;


}


@media only screen and (max-width: 600px) and (min-width: 350px) {
  .wrap
  {
    float: left;
    margin-left:0;
  }
 
.con
{

  margin-top: 12%;
  margin-left: 36%;
}

  .status
  {
        width: 160px;

    font-size: 1.2em;
    margin-top: 10%;
    
  }
  .b1
  {
    margin-top: 20%;
   margin-left: -36%;
  }

.center
{
 margin-top: 15%;
}
}






@media only screen and (max-width: 349px) and (min-width: 250px)
{
 
    .wrap
  {
    float: left;
    margin-left:0;
  }
 
.con
{

  margin-top: 12%;
}

  .status
  {
    font-size: 0.8em;
    margin-bottom: 2em;

  }
  .b1
  {
    margin-top: 5%;
  }

.center
{
 margin-top: 10%;
}

  }